<template>
	<div class="home">
		<h1 class="text-gray-100 text-4xl font-mono font-bold mt-16">🍻 SAUF.CHAT</h1>
		<p class="text-lg text-gray-600">Peer-to-peer group calls to hang out with your friends!</p>
		<div class="m-auto mt-24 max-w-lg bg-gray-800 rounded-lg shadow px-5 py-6 text-gray-100">
			<button class="w-full text-lg text-indigo-100 bg-indigo-500 hover:bg-indigo-400 active:bg-indigo-600 hover:text-white rounded p-3 mb-5 font-semibold" @click.prevent="createRoom">Create Room</button>

			<center class="text-gray-500 mb-5">or</center>

			<form class="w-full flex" @submit.prevent="joinRoom(roomName)">
				<input class="flex-1 text-lg bg-gray-100 text-gray-900 rounded px-3 py-3 mr-2" type="text" name="room" placeholder="Room Name" v-model="roomName">
				<button class="text-lg bg-indigo-500 rounded p-3 font-medium" type="submit">Join Room</button>
			</form>
		</div>
	</div>
</template>

<script>
// @ is an alias to /src
import cuid from 'cuid';

export default {
	name: 'home',
	data: () => ({
		roomName: ''
	}),
	methods: {
		createRoom() {
			const id = `R-${cuid()}`;
			this.joinRoom(id);
		},
		joinRoom(roomId) {
			console.log(this.$router);
			this.$router.push({ name: 'room', params: { roomId } });
		}
	}
};
</script>
